import { useState } from "react"
import { Container, FormControl, InputGroup } from "react-bootstrap"
import Categories from "../addIncomes/Categories"
import "./style.scss"

function IncomesHistory() {
  const [dateValue, setDateValue] = useState('');
  const [dateRangeStartValue, setDateRangeStartValue] = useState('');
  const [dateRangeEndValue, setDateRangeEndValue] = useState('');
  function dateReset(e) {
    setDateValue(e.target.value);
    setDateRangeStartValue('');
    setDateRangeEndValue('');
  }

  function handleChangeStart(e) {
    setDateValue('');
    setDateRangeStartValue(e.target.value);
  }
  function handleChangeEnd(e) {
    setDateValue('');
    setDateRangeEndValue(e.target.value);
  }

  return<>
    <Container className="pt-3">
      <div className="history-header">
        <div className="add-income-group">
          <select className="add-select">
            <option>Type</option>
            <option>Income</option>
            <option>Expense</option>
          </select>
          <Categories/>
        </div>
        <div className="select-date">
          <div className="one-date">
            <input type={'date'} value={dateValue} onChange={dateReset}></input></div>
          <div className="date-range-group">
            <input className="date-range" type={'date'} value={dateRangeStartValue} onChange={handleChangeStart}></input>
            <p className="mb-1">to:</p>
            <input className="date-range" type={'date'} value={dateRangeEndValue} onChange={handleChangeEnd}></input>
          </div>
        </div>
      </div>
      <div>Понедельник</div>
    </Container>
  </>
}

export default IncomesHistory