import { useEffect, useState } from "react"
import { Button, Container, Dropdown, DropdownButton, FormControl, InputGroup } from "react-bootstrap"
import { useParams } from "react-router-dom"
import Categories from "./Categories"
import LatestTransaction from "./LatestTransaction";
import "./style.scss";


function AddIncomes() {
  const [showModal, setShowModal] = useState(false);
  const [type, setType] = useState('');
  const [category, setCategory] = useState('');
  const [summ, setSumm] = useState('');
  const [coment, setComent] = useState('');
  const [date, setDate] = useState('');

  // const {userId} = useParams();
  const [user, setUser] = useState({});
  const [userId, setUserId] = useState('');
  useEffect(
    () => {
      let a = JSON.parse(localStorage.getItem('User'));
      setUserId(a?.id);
    Promise.all([
      new Promise (resolve => {
        fetch(`https://fakestoreapi.com/users/${a.id}`).then(data => data.json().then(data => resolve(data)))
      })
    ]).then(data => setUser(data[0]))
    let date = new Date;
    setDate(date.toJSON().slice(0, 10));
  }, [])

  function postData() {
    // fetch('https://fakestoreapi.com/users/', {
    //   method: 'POST',
    //   headers: {
    //     'Content-Type': 'application/json'
    //   },
    //   body: JSON.stringify({ 
    //     transaction_date: '2023-02-27',
    //     transaction_type: type,
    //     transaction_category__category_name: category,
    //     transaction_sum: summ,
    //     transaction_comment: coment
    //    })
    // })
    console.log(JSON.stringify({ 
          transaction_date: '2023-02-27',
          transaction_type: type,
          transaction_category__category_name: category,
          transaction_sum: summ,
          transaction_comment: coment
         }));
  }

  function saveType(e) {
    setType(e.target.value);
  }

  function saveCategory(e) {
    setCategory(e.target.value);
  }
  function saveSumm(e) {
    setSumm(e.target.value);
  }
  function saveComent(e) {
    setComent(e.target.value);
  }

// дату дабваить


  return <Container className="py-3">
    <div className="add-header">
      <h2>Add incomes / expenses</h2>
      <input type={'date'} defaultValue={date}></input>
    </div>
    <div className="add-income-group">
      <select className="add-select">
        <option>Type</option>
        <option>Income</option>
        <option>Expense</option>
      </select>
      <Categories/>
      <input type={'number'} placeholder='Price' className="add-input number"></input>
      <input type={'text'} placeholder='Notes' className="add-input text"></input>
      <button className="add-button">Add</button>
      {/* <Button variant="success" className="px-5" onClick={postData}>Add</Button> */}
    </div>

    <InputGroup className="d-none">
      <FormControl as='select' className="input-select" onChange={saveType}>
      <option>Type transaction</option>   
      <option>Income</option>   
      <option>Expense</option>   
      </FormControl>
      <Categories saveCategory={saveCategory}/>
      <FormControl placeholder="Price" className="input-price" onChange={saveSumm}/>
      <FormControl placeholder="Notes" onChange={saveComent}/>
      <Button variant="success" className="px-5" onClick={postData}>Add</Button>
    </InputGroup>
    <h2 className="mt-5">Latest transactions:</h2>
    <LatestTransaction showModal={showModal} setShowModal={setShowModal}/>
  </Container>
}

export default AddIncomes