import { Button, Table } from "react-bootstrap";
import ConfirmRemove from "./ConfirmRemove";
import deleteIcon from '../../img/AddIncomes/delete.svg';
import editIcon from "../../img/AddIncomes/edit.svg"

function TodayTransaction({ showModal, setShowModal }) {
  return <>
    <div className="latest-transaction">
      <div className="transaction-category text-center">
        <p className="m-0">Date</p>
      </div>
      <div className="transaction-price text-center">
        <p className="m-0">Income</p>
      </div>
      <div className="transaction-category text-center">
        <p className="m-0">Category</p>
      </div>
      <div className="transaction-price text-center">
        <p className="m-0">Price</p>
        {/* - or + */}
      </div>
      <div className="transaction-notes">
        <p className="m-0">Notes</p>
      </div>
      <button className="button-edit"><img src={editIcon}/></button>
      <button className="button-delete" onClick={() => setShowModal(true)}><img src={deleteIcon}/></button>
    </div>
    <Button variant="danger" className="d-none px-5" onClick={() => setShowModal(true)}>Remove</Button>
    {/* модальне вікно підтвердження */}
  <ConfirmRemove showModal={showModal} setShowModal={setShowModal}/>
  </>
}

export default TodayTransaction